function zoomIn() {

    let photos = document.querySelectorAll('.photos__img--item');
    let zoomDiv = document.querySelector('#zoom-div');
    let crossBtn = document.querySelector('#cross');
    let closeLink = document.querySelector('#close');

    photos.forEach(function(photo) 
    {
        photo.addEventListener('click', () => {
            zoomDiv.classList.toggle('none');
        })
    })

    crossBtn.addEventListener('click', () => 
    {
        zoomDiv.classList.toggle('none');
    })

    closeLink.addEventListener('click', () => 
    {
        zoomDiv.classList.toggle('none');
    })

}

zoomIn();

function zoomCopy() 
{

    let photos = document.querySelectorAll('.photos__img--item');
    
    photos.forEach(function(photo) 
    {
               
        photo.addEventListener('click', () => 
        {

            let photoCopy = document.querySelector('.zoom__img--item');

            let photoSrc = '';
            photoSrc = photo.src;
            
            photoCopy.src = photoSrc; 

        })

    })


}

zoomCopy();

